<template>
    <p-data-table
        :value="storageList"
        v-model:selection="selectedStorageList"
        selectionMode="single"
        responsiveLayout="scroll"
        showGridlines
        class="p-datatable-sm p-mb-1"
        :loading="loading"
    >
        <p-column-group type="header">
            <p-row>
                <p-column header="Cycle Id" />
                <p-column header="Freez Date" />
                <p-column header="Straw Id" />
                <p-column header="Stage / Grade of each embryo" colspan="8" />
                <p-column header="Dewar #" />
                <p-column header="Canister #" />
                <p-column header="Cryotop Color" />
                <p-column header="Cassette Info" colspan="3" />
                <p-column header="Survive #" />
                <p-column header="Develop #" />
                <p-column header="Thawing Date" />
                <p-column header="Discarding Date" />
                <p-column header="" />
            </p-row>
            <p-row>
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="Emb # 1" colspan="2" />
                <p-column header="Emb # 2" colspan="2" />
                <p-column header="Emb # 3" colspan="2" />
                <p-column header="Emb # 4" colspan="2" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
            </p-row>
            <p-row>
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="S" />
                <p-column header="G" />
                <p-column header="S" />
                <p-column header="G" />
                <p-column header="S" />
                <p-column header="G" />
                <p-column header="S" />
                <p-column header="G" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="Color" />
                <p-column header="Type" />
                <p-column header="Cassette ID" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
            </p-row>
        </p-column-group>

        <p-column field="cycleId" />
        <p-column field="freezDate">
            <template #body="{data}">
                {{ data.freezDate ? data.freezDate.split(" ")[0] : null }}
            </template>
        </p-column>
        <p-column>
            <template #body="{data}">
                {{ data.freezId }}
            </template>
        </p-column>
        <p-column field="embryo1s" />
        <p-column field="embryo1g" />
        <p-column field="embryo2s" />
        <p-column field="embryo2g" />
        <p-column field="embryo3s" />
        <p-column field="embryo3g" />
        <p-column field="embryo4s" />
        <p-column field="embryo4g" />
        <p-column field="dewar" />
        <p-column field="canisterNo" />
        <p-column field="plugColor">
            <template #body="{data}">
                <div style="padding: 4px" :style="style(data.plugColor)">
                    {{ data.plugColor }}
                </div>
            </template>
        </p-column>
        <p-column field="cassetteColor">
            <template #body="{data}">
                <div style="padding: 4px" :style="style(data.cassetteColor)">
                    {{ data.cassetteColor }}
                </div>
            </template>
        </p-column>
        <p-column field="cassetteType" />
        <p-column field="cassetteId" />
        <p-column field="embryoSurvived" />
        <p-column field="embryoDevelopment" />
        <p-column field="thawDate">
            <template #body="{data}">
                {{ data.thawDate ? data.thawDate.split(" ")[0] : null }}
            </template>
        </p-column>
        <p-column field="discardingDate" />
        <p-column header="">
            <template #body="{ data}">
                <span class="p-buttonset">
                    <p-button
                        class="p-button-sm"
                        icon="pi pi-print"
                        @click="printLabelJSPDF(data.freezId)"
                    />
                </span>
            </template>
        </p-column>
    </p-data-table>
</template>

<script>
import { useCryoStorageList } from "@/composable/cyro-storage";
import { onMounted } from "vue-demi";
import { cryoStorageStyle } from "@/service/utils";

import JsBarcode from "jsbarcode";
import jsPDF from "jspdf";
import { Canvg } from "canvg";

export default {
    setup() {
        const {
            storageList,
            patientInfo,
            selectedStorageList,
            loading,
            getStorageList
        } = useCryoStorageList();

        const printLabelJSPDF = async (strawId) => {
            console.log();
            try {
                const barcodelbl = strawId;
                const patient = patientInfo.value;
                const fullName = `${patient.chart} ${patient.firstName} ${patient.lastName}`;
                const dob = `DOB: ${patient.year}-${patient.month}-${patient.day}`;
                const starwId = `ID: ${strawId}`;

                const svg = document.createElement("svg");
                JsBarcode(svg, barcodelbl, {
                    format: "CODE128",
                    width: 4,
                    height: 40,
                    displayValue: false,
                    margin: 0
                });
                // Remove duplicate xmlns
                svg.removeAttribute("xmlns");
                // Render SVG to Canvas
                const canvas = document.createElement("canvas");
                canvas.width = 200;
                canvas.height = 40;
                const ctx = canvas.getContext("2d");
                const svgString = new XMLSerializer().serializeToString(svg);
                const canvgInstance = await Canvg.from(ctx, svgString);
                await canvgInstance.render();
                const pngDataUrl = canvas.toDataURL("image/png");
                // Create PDF
                const doc = new jsPDF({
                    orientation: "landscape",
                    unit: "mm",
                    format: [38, 10] // Label size
                });
                doc.setFontSize(4);
                doc.text(fullName, 1, 2);
                doc.setFontSize(4);
                doc.text(dob, 1, 3.5);
                doc.setFontSize(4);
                doc.text(starwId, 1, 5);
                doc.addImage(pngDataUrl, "PNG", 1, 6, 36, 3.5);
                // Generate PDF blob
                const pdfBlob = doc.output("blob");
                const blobUrl = URL.createObjectURL(pdfBlob);
                // Create hidden iframe
                const iframe = document.createElement("iframe");
                iframe.style.position = "fixed";
                iframe.style.right = "0";
                iframe.style.bottom = "0";
                iframe.style.width = "0";
                iframe.style.height = "0";
                iframe.style.border = "0";
                iframe.src = blobUrl;
                document.body.appendChild(iframe);
                iframe.onload = () => {
                    iframe.contentWindow.focus();
                    iframe.contentWindow.print();
                };
            } catch (err) {
                console.error("Print label error:", err);
            }
        };

        const style = cryoStorageStyle;

        onMounted(() => {
            getStorageList();
        });

        return {
            storageList,
            patientInfo,
            selectedStorageList,
            loading,
            style,
            printLabelJSPDF
        };
    }
};
</script>
