<template>
    <h4>Received Eggs</h4>

    <p-data-table
        :value="fertilizationLines"
        :loading="loading"
        responsiveLayout="scroll"
        showGridlines
        class="p-datatable-sm p-mb-1"
        :rowClass="getRowClass"
    >
        <p-column header="Source" field="source" />
        <p-column header="Freez Date" field="frozenDate" />
        <p-column header="Straw Id" field="strawId" />
        <p-column header="Number" field="noEmbryo" />
        <p-column header="Fate" field="fate" />
        <p-column header="Thawing Date" field="thawDate" />
    </p-data-table> 
    <div class="p-mt-2">
        <p-button
            label="Report"
            @click="showCryoStoragePrintPage"
            class="p-mr-2 p-button-primary p-button-md"
        />
    </div>
</template>

<script>
import { useReceivedEgss } from "@/composable/cyro-storage";
import { NL_FORM_BASE } from "@/constants";
import { useStore } from "vuex";
import { onMounted } from "vue-demi";

export default {
    setup() {
        const store = useStore();
        const {
            fertilizationLines,
            loading,
            getFertilizationLines
        } = useReceivedEgss();

        onMounted(() => {
            getFertilizationLines();
        });

        return { 
            fertilizationLines, 
            loading, 
            getRowClass(data) {
                let resp = {
                    "row-red": data.fate === "Transfer",
                    "row-gray": data.fate === "Thawed"
                };

                // console.log(resp);
                return resp;
            },
            // showCryoStoragePrintPage: () => {
            //     const url =
            //         NL_FORM_BASE +
            //         "/cycles-reports/print-cycle-monitoring.php?cycleId=" +
            //         store.state.artCycle.selectedCycle.cycleId +
            //         "&patientId=" +
            //         store.state.artCycle.selectedCycle.patientId;
            //     // "&apiToken=" +
            //     // store.state.user.accessToken;

            //     window.open(url);
            // }, 
        };
    }
};
</script>
<style scoped>
:deep() .row-red {
    background-color: red !important;
}
:deep() .row-gray {
    background-color: gray !important;
}
</style>
